.card{
    margin-bottom: 30px;
    border-radius: 15px !important;
    background-color: rgba(245,245,245,0.2) !important;
    color: #f5f5f5;
    height: 400px;
}

.card:hover{
    background-color: rgba(245,245,245,0.35) !important;
    transform: scale(1.05);
    transition: 0.15s;
    box-shadow: 5px 10px 15px #0c0c0c;
}

.image{
    padding: 20px;
    max-width: 250px;
    max-height: 190px;
    margin: auto;
    object-fit: cover;
}

.title{
    margin-bottom: 0;
    text-align: center;
    letter-spacing: 0;
}

.team{
    width: calc(100% - 30px);
    margin-top: 35px;
    align-items: center;
    bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
}

.teamName{
    margin: 0;
    display: inline-block;
}

.teamLogoImage{
    max-width: 45px;
    display: inline-block;
}

@media all and (max-width: 1200px) {

}

@media all and (max-width:576px){
    .card:hover{
        transform: scale(1.01) !important;
        transition: 0.15s;
    }
}